<template>
<div>
  <div v-if="step == 0">
   <Post v-for="(a,i) in pData" :key="i"  :a="a" :num="i"/>
  </div>

  <!-- ÌïÑÌÑ∞ÏÑ†ÌÉùÌéòÏù¥ÏßÄ -->
  <div v-if="step == 1">
    <div :class="c +' upload-image'" :style="{backgroundImage : `url(${b})`}"></div>
    <div class="filters">
      <FilterBox :b="b" v-for="(c,i) in pFilter" :key="i" :c="c">
        {{c}}
      </FilterBox>
    </div>
  </div>
  <!-- Í∏ÄÏûëÏÑ±ÌéòÏù¥ÏßÄ -->
  <div v-if="step == 2">
    <div :class="c +' upload-image'" :style="{backgroundImage : `url(${b})`}"></div>
    <div class="write">
      <textarea @input="$emit('write',$event.target.value)" class="write-box">write!</textarea>
    </div>
  </div>
  <div v-if="step == 3">
    <MyPage/>
  </div>
</div>
</template>

<script>
import Post from './Post.vue'
import FilterBox from './FilterBox.vue'
import MyPage from './MyPage.vue'
import {mapState} from 'vuex'

export default {
  components: {
    Post
    ,FilterBox
    ,MyPage
  }
  ,props:{
      data:Array
      ,step:Number
      ,b:String
      ,c:String
  }
  ,computed:{
    pData(){
      return this.$store.state.postData
    }
    ,pFilter(){
      return this.$store.state.filter
    }
  },
  ...mapState(['pData', 'pFilter'])
}

</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>