    publish(){
      var plusPost = {
        name: "Kim kimkim",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.b,
        likes: 0,
        date: new Date(),
        liked: false,
        content: this.plus,
        filter: this.c
      };
      this.$store.state.postData.unshift(plusPost);
      this.step = 0;
    }
    ,upload(e){
      let a  = e.target.files;
      this.b = URL.createObjectURL(a[0]);
      this.step = 1;
    }
  }
}
