<template>
<div :class="c+' filter-item'" :style="{backgroundImage : `url(${b})`}" @click="fire">
    <slot></slot>
</div> 
</template>

<script>
export default {
    name : 'filterBox',
    props:{
      b:String
      ,c:String
  }
  ,methods :{
      fire(){
          this.emitter.emit('filterP',this.c);
      }
  }
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
  cursor:pointer;
}
</style>